body{
  font-family: 'Poppins', sans-serif;
  margin: 0;
}

nav{
  height: 60px;
  display: flex;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
}

nav .nav--logo{
  max-width: 100%;
}

.hero {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.hero img{
    max-width: 100%;
    height: auto;
}
.hero .hero-text{
  text-align: center;
}


.cards-list{
  display: flex;
}
.card{
  width: 200px;
  padding: 1rem;
  flex: 0 0 auto;
  flex-direction: column;
  position: relative;
}
.card-stat img{
  width: 13px;
}
.card .card-f-img{
  max-width: 100%;
  height: auto;
  border-radius: 0.5rem;
}
.card p{
  font-size: 0.7rem;
}
.card-badge{
    position: absolute;
    top: 25px;
    left: 25px;
    background-color: #fff;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 10px;
    border: #f5f5f5 solid 1px;
}
